  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    "Helvetica Neue",
    Arial,
    sans-serif;
  padding: 25px;
  background-color: azure;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
}

h1 {
  font-size: 50px;
  text-align: center;
  margin-bottom: 30px;
  color: rgba(255, 255, 255, 0.955);
}

a {
  color: inherit;
}

input {
  padding: 10px 190px 10px 20px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 25px;
  font-family: inherit;
}

button {
  padding: 10px 25px;
  font-size: 16px;
  border: none;
  border-radius: 25px;
  background-color: #d3d6d875;
  color: white;
  cursor: pointer;
  font-family: inherit;
}

.main-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow:
    0 5px 10px rgba(0, 0, 0, 0.2),
    0 15px 40px rgba(0, 0, 0, 0.05);
  padding: 20px;
  max-width: 500px;
  width: 100%;
  margin-top: 20px;
  align-items: center;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.location {
  font-size: 24px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.location h2 {
  font-size: 32px;
  margin-bottom: 15px;
  color: #333;
}

.location img {
  width: 80px;
  height: 80px;
  margin-bottom: 10px;
}

.location p {
  font-size: 24px;
  color: #555;
  margin: 5px 0;
}

.location .temp-row {
  display: flex;
  gap: 20px;
}

.main-container > p {
  font-size: 18px;
  color: #666;
  margin: 10px 0;
  text-align: center;
}

.main-container h2 {
  font-size: 28px;
  color: #333;
  text-align: center;
}

.main-container .current-temp {
  font-size: 48px;
  font-weight: bold;
  color: rgba(80, 104, 149, 0.856);
  border: 2px solid black;
  border-radius: 15px;
  padding: 10px;
  box-shadow:
    0 5px 10px rgba(0, 0, 0, 0.2),
    0 15px 40px rgba(0, 0, 0, 0.05);
}

.footer {
  margin-top: 30px;
  text-align: center;
  color: white;
  font-size: 14px;
}

.high-temp {
  font-weight: bolder;
}

.sub-container {
  display: grid;
  grid-template: 1fr 1fr / 1fr 1fr;
  gap: 20px;
  margin-top: 20px;
  background-color: white;
  border-radius: 15px;
  box-shadow:
    0 5px 10px rgba(0, 0, 0, 0.2),
    0 15px 40px rgba(0, 0, 0, 0.05);
  padding: 20px;
  max-width: 500px;
  width: 100%;
}

.extra-info {
  display: contents;
}

.extra-info div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 10px;
  background-color: #e8e6e6b3;
}

.loading {
  text-align: center;
  font-size: 1.2rem;
  color: gray;
}
